.contact {
  padding: 100px 0;
  background-color: #f9f9f9;
}

.contact-container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 24px;
}

.contact-heading {
  text-align: center;
  margin-bottom: 60px;
}

.contact-heading h2 {
  font-size: 36px;
  font-weight: 700;
  color: #1a1a1a;
  margin-bottom: 16px;
}

.contact-content {
  display: flex;
  justify-content: space-between;
  gap: 40px;
}

.contact-info {
  flex: 1;
}

.contact-info h3 {
  font-size: 24px;
  font-weight: 600;
  color: #1a1a1a;
  margin-bottom: 16px;
}

.contact-info p {
  font-size: 16px;
  line-height: 1.6;
  color: #555;
  margin-bottom: 24px;
}

.contact-details {
  margin-top: 30px;
}

.contact-item {
  display: flex;
  align-items: center;
  margin-bottom: 16px;
}

.contact-icon {
  font-size: 24px;
  margin-right: 16px;
}

.contact-item p {
  margin-bottom: 0;
}

.contact-form {
  flex: 1;
  background-color: #fff;
  padding: 30px;
  border-radius: 8px;
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.05);
}

.form-group {
  margin-bottom: 20px;
}

.form-group label {
  display: block;
  font-size: 16px;
  font-weight: 500;
  margin-bottom: 8px;
  color: #1a1a1a;
}

.form-group input,
.form-group textarea {
  width: 100%;
  padding: 12px;
  border: 1px solid #ddd;
  border-radius: 4px;
  font-size: 16px;
  transition: border-color 0.3s ease;
}

.form-group input:focus,
.form-group textarea:focus {
  outline: none;
  border-color: #4da8da;
}

.contact-form .btn {
  width: 100%;
  margin-top: 10px;
}

@media screen and (max-width: 768px) {
  .contact-content {
    flex-direction: column;
  }
  
  .contact-info {
    margin-bottom: 30px;
  }
} 