.services {
  padding: 100px 0;
  background-color: #fff;
}

.services-container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 24px;
}

.services-heading {
  text-align: center;
  margin-bottom: 60px;
}

.services-heading h2 {
  font-size: 36px;
  font-weight: 700;
  color: #1a1a1a;
  margin-bottom: 16px;
}

.services-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  gap: 30px;
}

.service-card {
  background-color: #fff;
  border-radius: 8px;
  padding: 30px;
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.05);
  transition: transform 0.3s ease, box-shadow 0.3s ease;
  text-align: center;
}

.service-card:hover {
  transform: translateY(-10px);
  box-shadow: 0 15px 30px rgba(0, 0, 0, 0.1);
}

.service-icon {
  font-size: 48px;
  margin-bottom: 20px;
}

.service-card h3 {
  font-size: 22px;
  font-weight: 600;
  color: #1a1a1a;
  margin-bottom: 16px;
}

.service-card p {
  font-size: 16px;
  line-height: 1.6;
  color: #555;
}

@media screen and (max-width: 768px) {
  .services-grid {
    grid-template-columns: 1fr;
  }
} 