.about {
  padding: 100px 0;
  background-color: #f9f9f9;
}

.about-container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 24px;
}

.about-heading {
  text-align: center;
  margin-bottom: 60px;
}

.about-heading h2 {
  font-size: 36px;
  font-weight: 700;
  color: #1a1a1a;
  margin-bottom: 16px;
}

.underline {
  width: 80px;
  height: 4px;
  background-color: #4da8da;
  margin: 0 auto;
}

.about-content {
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 40px;
}

.about-text {
  flex: 1;
}

.about-text h3 {
  font-size: 24px;
  font-weight: 600;
  color: #1a1a1a;
  margin-bottom: 16px;
}

.about-text p {
  font-size: 16px;
  line-height: 1.6;
  color: #555;
  margin-bottom: 24px;
}

.about-image {
  flex: 1;
  border-radius: 8px;
  overflow: hidden;
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.1);
}

.about-image img {
  width: 100%;
  height: auto;
  display: block;
}

@media screen and (max-width: 768px) {
  .about-content {
    flex-direction: column;
  }
  
  .about-image {
    order: -1;
    margin-bottom: 30px;
  }
} 