.hero {
  background: linear-gradient(to right, rgba(0, 0, 0, 0.7), rgba(0, 0, 0, 0.7)), 
              url('https://images.unsplash.com/photo-1475070929565-c985b496cb9f?ixlib=rb-1.2.1&auto=format&fit=crop&w=1950&q=80');
  height: 100vh;
  background-position: center;
  background-size: cover;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 0 30px;
  position: relative;
  color: #fff;
}

.hero-container {
  max-width: 1200px;
  padding: 0 24px;
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
}

.hero h1 {
  font-size: 48px;
  margin-bottom: 24px;
  font-weight: 700;
}

.hero p {
  font-size: 24px;
  margin-bottom: 32px;
  max-width: 600px;
}

.hero-image {
  width: 300px;
  height: 300px;
  margin-bottom: 32px;
  border-radius: 10px;
  overflow: hidden;
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.3);
}

.hero-image img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.hero-btns {
  display: flex;
  flex-direction: row;
  gap: 16px;
}

.btn {
  padding: 12px 24px;
  font-size: 16px;
  font-weight: 600;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  transition: all 0.3s ease;
}

.btn-primary {
  background-color: #4da8da;
  color: #fff;
}

.btn-primary:hover {
  background-color: #3a8bbd;
  transform: translateY(-2px);
}

.btn-outline {
  background-color: transparent;
  color: #fff;
  border: 2px solid #fff;
}

.btn-outline:hover {
  background-color: #fff;
  color: #1a1a1a;
  transform: translateY(-2px);
}

@media screen and (max-width: 768px) {
  .hero h1 {
    font-size: 32px;
  }
  
  .hero p {
    font-size: 18px;
  }
  
  .hero-image {
    width: 250px;
    height: 250px;
  }
  
  .hero-btns {
    flex-direction: column;
  }
} 